import React from 'react';


import { 
	Card,
	CardBody,
	Row,
	Col,
	CardHeader,

	} from "reactstrap";

import { Line } from "react-chartjs-2";

import {
  chartExample1,
} from "variables/charts.js";


const Graphs = (props) => {
	const { posts } = props;
	let costs = [];
	let dates = [];

	if (!posts || posts.length === 0) return <p>Can not find any posts, sorry</p>;
	let wiu = posts.sort(function(a,b){
		return new Date(b['horaInicio'])-new Date(a['horaInicio']);
	})
	for (let i = 0; i < wiu.length; i++) {
		const e = wiu[i];
		costs.push(e['costoLlamada'])
		dates.push(e['horaInicio'])
	}
	let data={
		labels: dates,
		datasets: [
			{
				label: "First dataset",
				data: costs,
				fill: true,
				backgroundColor: "rgba(75,192,192,0.2)",
				borderColor: "rgba(75,192,192,1)"
			},
		]
	};
	return (
		<React.Fragment>
			 <Row className="mt-5">
          <Col className="mb-5 mb-xl-0" xl="12">
            <Card className="bg-gradient-default shadow">
              <CardHeader className="bg-transparent">
                <Row className="align-items-center">
                  <div className="col">
                    <h6 className="text-uppercase text-light ls-1 mb-1">
                      Overview
                    </h6>
                    <h2 className="text-white mb-0">Sales value</h2>
                  </div>
                  <div className="col">
                  </div>
                </Row>
              </CardHeader>
              <CardBody>
                {/* Chart */}
                <div className="chart">
                  <Line
                    data= {data}
                    options={chartExample1.options}
                    getDatasetAtEvent={(e) => console.log(e)}
                  />
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
		</React.Fragment>
	);
};
export default Graphs;