import React, {useEffect, useState} from 'react';
import './App.css';
import Posts from './components/Posts';
import PostLoadingComopnent from './components/PostLoading';
import axios from 'axios';

function App(){

  const PostLoading =PostLoadingComopnent(Posts);
  const [appState, setAppState] = useState({
    loading:false,
    posts:null,
  });

   const getPosts = async () => {
    setAppState({loading:true});
    try{
      const userPosts = await axios.get('https://dashboard-fede-django.herokuapp.com/api/')
      setAppState({loading:false, posts:userPosts.data});
    }catch(err){
      console.error(err.message);
    }
  }; 


  useEffect(()=> {
    getPosts()
    
    const interval = setInterval(()=>{
      getPosts()
    },10000)

    return () => clearInterval(interval)
  },[setAppState]);
  return(
    <div className="App">
      <h1>Estos datos se actualizan cada 10 segundos</h1>
      <PostLoading isLoading={appState.loading} posts={appState.posts} />
    </div>
  );


}

export default App;