import React, {useEffect, useState} from 'react';
import './App.css';
import Graphs from './components/Graphs';
import GraphLoadingComopnent from './components/GraphLoading';
import axios from 'axios';


function GraphApp(){



  const GraphLoading =GraphLoadingComopnent(Graphs);
  const [appState, setAppState] = useState({
    loading:false,
    posts:null,
  });

   const getPosts = async () => {
    setAppState({loading:true});
    try{
      const costoLlamada = await axios.get('https://dashboard-fede-django.herokuapp.com/api/')
      setAppState({loading:false, posts:costoLlamada.data});

    }catch(err){
      console.error(err.message);
    }
  }; 


  useEffect(()=> {
    getPosts()
    
    // const interval = setInterval(()=>{
    //   getPosts()
    // },10000)

    // return () => clearInterval(interval)
  },[setAppState]);
  return(
    <div className="Graph">
      <GraphLoading isLoading={appState.loading} posts={appState.posts} />
    </div>
  );


}

export default GraphApp;